.grid {
    display: grid;
    grid-template-columns: repeat(3, minmax(100px, 1fr));
    padding-left: 20%;
    padding-right: 20%;
    gap: 20px;
    padding-top: 70px;
}

.phoneImg {
    
    
    object-fit: contain;
    height: 250px;
}

.phone {
    font-family: 'Lato', sans-serif;
    display: flex;
    flex-direction: column;
    margin-top: 60px;
    align-items: center;
    gap: 10px;
    font-weight: 600;
    background: #f5f5f4;
    padding: 10px;
    border-radius: 20px;
    
}

.phone:hover {
    border: 1px solid gray;
   
}

.phoneImg:hover {
   
    cursor: pointer;
}


.addToCartBtn {
    align-items: center;
    appearance: none;
    background-color: #fff;
    border-radius: 24px;
    border-style: none;
    box-shadow: rgba(0, 0, 0, .2) 0 3px 5px -1px,rgba(0, 0, 0, .14) 0 6px 10px 0,rgba(0, 0, 0, .12) 0 1px 18px 0;
    box-sizing: border-box;
    color: #3c4043;
    cursor: pointer;
    display: inline-flex;
    fill: currentcolor;
    font-family: "Google Sans",Roboto,Arial,sans-serif;
    font-size: 14px;
    font-weight: 500;
    height: 40px;
    justify-content: center;
    letter-spacing: .25px;
    line-height: normal;
    max-width: 100%;
    overflow: visible;
    padding: 2px 24px;
    position: relative;
    text-align: center;
    text-transform: none;
    transition: box-shadow 280ms cubic-bezier(.4, 0, .2, 1),opacity 15ms linear 30ms,transform 270ms cubic-bezier(0, 0, .2, 1) 0ms;
    user-select: none;
    -webkit-user-select: none;
    touch-action: manipulation;
    width: 200px;
    will-change: transform,opacity;
   
}

.addToCartBtn:hover {
    background: #F6F9FE;
    color: #174ea6;
  }
  
.addToCartBtn:active {
    box-shadow: 0 4px 4px 0 rgb(60 64 67 / 30%), 0 8px 12px 6px rgb(60 64 67 / 15%);
    outline: none;
  }
  
.addToCartBtn:focus {
    outline: none;
    border: 2px solid #4285f4;
  }


  /* Media queries */
  @media(max-width: 960px){
    .phoneImg {
        max-width: 100%;
    }
  }

  @media(max-width: 600px){
    .addToCartBtn {
        font-size: 10px;
    }

    .grid {
        padding: 0%;
        padding-top: 70px;
        gap: 10px;
    }

    .phone {
        height: 300px;
       margin-top: 20px;
    }
  }